@include breakpoint-min(sm) {
    .header {
        position: absolute;
        width: 98vw;
        &__logo {
            display: flex;
            justify-content: center;
            font-family: $font-family-secondary;
            margin: 2.5rem 4rem;
            h2 {
                font-size: $size-xxl;
            }
        }
        &__nav {
            display: flex;
            justify-content: center;
            list-style: none;
            margin: 0 3.5rem;
            &-btn {
                font-size: $size-sm;
                margin: 5rem 1.5rem;
                display: block;
                font-weight: bold;
                color: $color-darkest;
            }
        }
    }
}
@include breakpoint-min(lg) {
    .header {
        display: flex;
        &__logo {
            justify-content: flex-start;
            flex: 1;
            margin: 5rem 7.5rem;
            h2 {
                font-size: $size-xxl;
            }
        }
        &__nav {
            flex: 1;
            justify-content: flex-start;
            margin: 0 5.5rem;
        }
    }
}
@include breakpoint-min(xl) {
    .header {
        &__logo {
            margin: 5rem 19rem;
            h2 {
                font-size: $size-xxxl;
            }
        }
        &__nav {
            margin: 0 16.5rem;
            &-btn {
                transition: 500ms;
                border-top: 2px solid transparent;
                &:hover {
                    border-top: 2px solid $outrageous-orange;
                }
            }
        }
    }
}