.gallery {
    background-color: $bright-sun;
    padding: 0 0 11.5rem;
    .width-size{ max-width: 126rem;}
    &__grid {
        display: grid;
    }
    
    .grid-container {
        display: grid; 
        grid-template-columns: minmax(0, 29.2rem) minmax(0, 28rem) minmax(0, 20rem) minmax(0, 37rem); 
        grid-template-rows: minmax(auto, 13.7rem) minmax(auto, 13.8rem) minmax(auto, 12.7rem) minmax(auto, 13.8rem) minmax(auto, 20rem) minmax(auto, 20rem); 
        max-width: 117rem;
        margin: 0 auto;
        img {
            width: 100%;
            padding: 1.5rem;
            max-height: 100%;
        }
      }
      
      .grid1 { grid-area: 1 / 1 / 5 / 4; }
      
      .grid2 { grid-area: 5 / 1 / 7 / 3; }
      
      .grid3 { grid-area: 5 / 3 / 7 / 5; }
      
      .grid4 { grid-area: 3 / 4 / 5 / 5; }
      
      .grid5 { grid-area: 1 / 4 / 3 / 5; }
