.services{
    background-color: #70bfff;
    &__img{
        background-image: url(../img/pomidory.png);
        background-repeat: no-repeat;
        background-position: 0% 50%;
        display: flex;
        align-items: center;
    }
    &__nav{
        list-style: none;
        font-size: $size-lg;
        font-weight: bold;
        padding: 18rem 0 15rem 26rem;
    }
    &__btn{
        text-decoration: none;
        position: relative;
        color: $color-darkest;
        max-width: 40rem;
        max-height: 10rem;
        display: block;
        line-height: 2.7rem;
        padding: 3rem 10rem 3rem 3rem;
        text-align: center;
        transition: 500ms;
        margin: 1rem;
        overflow: hidden;
        &:hover{
            background-color: $color-light;
        }
        img{
            position: absolute;
            right: 0;
            top: 0;
            padding: 3.3rem 3rem;
            max-width: 10rem;;
            border-left: 1px solid $color-dark;
        }
    }
}