.subcribe {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-position: 50% 100%;
    height: 50%;
    background-repeat: no-repeat;
    background-image: url('../../dist/poziomkisubskrybcja.68b7df32.jpg');
    .width-size {
        flex: 1;
        padding: 0 16.5rem;
    }
    .title{
        font-size: 4rem;
        color: $color-darkest;
        margin: 0;
    }
    .text {
        max-width: 57rem;
        margin: 4rem 0 4rem 0;
    }
    .btn {
        width: 17rem;
        margin: 0 0 0 3rem;
    }
    &__input {
        width: 57rem;
        height: 5.5rem;
        color: grey;
        display: inline-block;
        font-size: 1.5rem;
    }

}