.blog {
    background-color: $heliotrope;
    padding: 0 0  11.5rem 0;
    .title{
        align-items: center;
    }
    .text {
        text-align: center;
        padding: 5rem 0;
        max-width: 57rem;
        margin: 0 auto;
    }
    &__photos {
        max-width: 120rem;
        margin: 0 auto;
        img {
            padding: 0 1.5rem;
        }
    }
    .btn {
        margin: 5rem auto;
    }
}