.contact {
    background-image: url('./../../src/img/footer.jpg');
    background-attachment: fixed;
    background-size: cover;
    background-position: 50% 50%;
    max-height: 100%;
    width: 100%;
    padding: 0 0 15rem 0;
    .text {
        max-width: 37rem;
        margin: 0 auto;
        color: $color-darkest;
    }
    &__message {
        padding: 5rem 8.5rem 5rem 16.5rem;
        input {
            height: 5rem;
            max-height: 5rem;
            width: 50rem;
            max-width: 50rem;
            border: none;
            margin: 1.5rem 0 0 0;
            opacity: 0.5;
            background-color: $color-primary;
            color: $color-lighter;
            &:focus {
                opacity: 1;
            }
        }
    }
    &__info {
        color: $color-darkest;
        font-size: 2rem;
        padding: 6rem 0 5rem 8.5rem;
        h3 {
            font-weight: bold;
        }
    }
}