@include breakpoint-min(xl) {
    .hero{
        .width-size{
            flex: 1;
            padding: 0 16.5rem;
        }
        background-image: url(../img/header.jpg);
        background-position: top;
        height: 100%;
        padding: 0 16.5rem;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        &__title{
            font-weight: 900;
            font-size: $size-xxxxl;
        }
        .text{
            margin: 3.5rem 0 3rem 0;
            max-width: 36rem;
        }
    }
}